/*
 * XWOO Front CSS
 * File Description: The main stylesheet of this plugins for main design part.
 */
.search_form_shortcode {
    button {
        width: 188px;
        font-size: 16px;
        line-height: 23px;
    }
    .xwoo-ajax-search, 
    .xwoo-products-search-results {
        width: calc(100% - 198px);
        margin-right: 10px;
        img {
            width: 40px;
            height: 40px;
        }
        .xwoo-productss-search.results-list {
            display: inline-block;
            background: #fff;
            background-color: #fff;
            box-shadow: 0 3px 5px 0 #e0e0e0;
            border-radius: 3px;
            margin-bottom: 50px;
            -webkit-transition: .3s;
            transition: .3s;
            margin: 0;
            padding: 20px 20px 10px;
            li {
                display: inline-block;
                a {
                    text-decoration: none;
                    font-size: 18px;
                }
                .pack-thumb {
                    display: inline-flex;
                    img {
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}
